type Invoice = {
  id: string
  number: string
  company: string
  amount: string
  status: 'Bezahlt' | 'Offen' | '√úberf√§llig'
}

const invoices: Invoice[] = [
  { id: '1', number: 'RG-2024-001', company: 'M√ºller GmbH', amount: '‚Ç¨2,450', status: 'Bezahlt' },
  { id: '2', number: 'RG-2024-002', company: 'Schmidt AG', amount: '‚Ç¨1,890', status: 'Offen' },
  { id: '3', number: 'RG-2024-003', company: 'Weber KG', amount: '‚Ç¨3,200', status: '√úberf√§llig' }
]

function StatusBadge({ status }: { status: Invoice['status'] }) {
  const klass = status === 'Bezahlt' ? 'bg-green-100 text-green-700' : status === 'Offen' ? 'bg-yellow-100 text-yellow-700' : 'bg-red-100 text-red-700'
  return <span className={`px-2 py-1 text-xs rounded ${klass}`}>{status}</span>
}

