import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  Filler
} from 'chart.js'
import { Line } from 'react-chartjs-2'

ChartJS.register(CategoryScale, LinearScale, PointElement, LineElement, Title, Tooltip, Legend, Filler)

export default function LineChart() {
  const data = {
    labels: ['Jan', 'Feb', 'M√§r', 'Apr', 'Mai', 'Jun'],
    datasets: [
      {
        label: 'Umsatz',
        data: [12000, 18000, 22000, 26000, 30000, 36000],
        borderColor: '#2563eb',
        backgroundColor: 'rgba(37,99,235,0.15)',
        fill: true,
        tension: 0.3
      }
    ]
  }

  const options = {
    responsive: true,
    plugins: {
      legend: { display: false }
    }
  }

  return (
    <div className="bg-white p-4 rounded shadow-sm">
      <div className="text-sm text-gray-500 mb-3">Umsatzentwicklung</div>
      <Line options={options} data={data} />
    </div>
  )
}
